"use client";

import React, { useEffect, useState } from "react";
import { getAllNews } from "@/lib/client/actions";
import Image from "next/image";

interface NewsItem {
  id: string;
  title: string;
  desc: string;
  image?: string;
  imageBefore?: string;
  imageAfter?: string;
}

const News = () => {
  const [news, setNews] = useState<NewsItem[]>([]);
  const [page, setPage] = useState(1);
  const limit = 6;

  useEffect(() => {
    const fetchNews = async () => {
      try {
        const { news } = await getAllNews(page, limit);
        setNews(news);
      } catch (error) {
        console.error("Failed to fetch news:", error);
      }
    };

    fetchNews();
  }, [page]);

  return (
    <div className="mt-[62px] md:mt-[95px] p-2 sm:p-3 w-full h-full flex flex-col gap-20 xl:gap-28 justify-center items-center py-16 xl:py-20 relative">
      <h1 className="mb-5 text-3xl md:text-4xl lg:text-5xl font-bold text-center">
        Seneste nyt <span className="text-primary"> i haverne </span>
      </h1>
      <div className="md:max-w-lg lg:max-w-3xl xl:max-w-4xl flex flex-col gap-10 md:gap-16">
        {news.map((item) => (
          <div
            key={item.id}
            className="card lg:card-side bg-base-100 shadow-md rounded-lg lg:h-72 xl:h-[350px]"
          >
            <figure className="w-full lg:w-1/2">
              {item.imageBefore && item.imageAfter ? (
                <div className="diff aspect-[16/9]">
                  <div className="diff-item-1">
                    <Image
                      src={item.imageBefore || ""}
                      alt={`${item.title} før`}
                      width={500}
                      height={300}
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                  <div className="diff-item-2">
                    <Image
                      src={item.imageAfter || ""}
                      alt={`${item.title} efter`}
                      width={500}
                      height={300}
                      style={{ objectFit: "cover" }}
                    />
                  </div>
                  <div className="diff-resizer"></div>
                </div>
              ) : (
                <div className="relative w-full h-full pb-[56.25%] overflow-hidden">
                  <Image
                    src={item.image || ""}
                    alt={item.title}
                    fill
                    sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                    style={{ objectFit: "cover" }}
                  />
                </div>
              )}
            </figure>
            <div className="card-body w-full lg:w-1/2">
              <h2 className="card-title">{item.title}</h2>
              <p>{item.desc}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default News;
